body {
  margin: 0;
  font-family: 'Roboto', 'Arial', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  direction: rtl;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

.data-grid-container {
  height: 600px;
  width: 100%;
}

.chart-container {
  margin-top: 20px;
  margin-bottom: 20px;
}

.filter-container {
  margin-bottom: 20px;
}

.dashboard-container {
  padding: 20px;
}

.dashboard-title {
  margin-bottom: 20px;
  text-align: center;
}

.dashboard-card {
  margin-bottom: 20px;
  padding: 15px;
}

.dashboard-tabs {
  margin-bottom: 20px;
}

.stat-card {
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.stat-title {
  font-size: 1.1rem;
  color: #555;
}

.stat-value {
  font-size: 2rem;
  font-weight: bold;
  margin: 10px 0;
}

.stat-change {
  font-size: 0.9rem;
}

.positive-change {
  color: #4caf50;
}

.negative-change {
  color: #f44336;
}
